	module.exports = {"data":{"markdownRemark":{"html":"<p>In the early morning, I watched a original film called \"The Past\", which seems to be a film promoting religious spirit. Say a child likes to sing, under the guidance of the priest, want to be a big singer. Later, in a car accident, the scorpion lost his voice and could not sing again. The child worshipped God with one heart and suddenly recovered his voice. Have a cultural background.</p>\n<p>Write an article on \"Revelation of Revelation.\" The title is defined as \"the creation of the martial arts, the cultivation of the martial law.\" This article is not very good to write, can not be too theoretical, there must be a certain plot. Write how to debate and train, the problem of training, not written in the past, training is difficult to write, training is an operational problem, not a theoretical problem, it is a contingency process, not a preaching process. The training should be adjusted according to the situation found anywhere, anytime, and the training plan should be formulated according to the student's situation. After each training and debate, there is no deep impression, no object. To have an object to have a feeling, this is the real training.</p>\n<p>In the evening, I saw a video called \"ALIEN\", translated into Chinese \"Amorphous\", which is the first episode. I saw the second episode in Tokyo in 1992, so I am interested in seeing the first episode. A science fiction film, saying that a commercial spacecraft on the earth returned more than 20 million tons of ore in a galaxy, and received strange waves on the way. When it was checked, there was something that was brought to the spaceship, which was a special shape. . Alien is a ferocious creature that is so powerful that it kills all people and leaves only a woman. Later she finally shot the alien from the spaceship. The next episode follows this story, saying that the alien is not dead, still there.</p>\n<p>Such films have a large market in Western countries. I don’t know whether it is the cause of mentality or the cause of society. In developing countries, it may not be.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0015.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0015.md","frontmatter":{"title":"Sunday on January 23rd"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0015.md absPath of file >>> MarkdownRemark"}}

